.ability-container {
    width: 100%;
    height: 60vh;
    background-color: antiquewhite;
    display: flex;
    flex-wrap: wrap;

    h1 {
        margin: 0;
        font-family: 'Montserrat-Bold';
    }
}

@media only screen and (min-width: 321px) and (max-width: 480px) {
    .ability-container {
        flex-direction: column;

        .ability-container__image {
            width: 100%;
            height: 50%;

            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }

        .ability-container__content {
            width: 100%;
            max-width: 100%;
            height: 50%;
            display: flex;
            justify-content: center;
            flex-direction: column;
            text-align: left;
            text-wrap: wrap;
            gap: 15px;

            h1 {
                color: black;
                font-size: 1.35em;
                text-align: center;
            }

            .ability-container__content-list {
                display: flex;
                width: 100%;
                text-align: left;
                justify-content: start;
                align-items: start;
                align-content: start;

                h5 {
                    text-align: center;
                }

                ul {
                    list-style: none;
                    width: 90%;
                    text-align: left;
                    display: flex;
                    flex-direction: column;
                    justify-content: flex-start;
                    align-content: flex-start;
                }

                li {
                    color: black;
                    font-size: 0.5em;
                    width: 90%;
                    max-width: 100%;
                    text-wrap: wrap;
                    letter-spacing: 0.5px;
                    text-align: left;
                    margin-bottom: 5px;
                    margin-top: 3px;
                    margin-left: 15px;
                }
            }

        }
    }
}

